<template>
  <div class="header">
    <span @click="$router.back()" class="iconfont iconjiantou2"></span>
    <div class="title">{{title}}</div>
  </div>
</template>

<script>
export default {
  props: ['title']
}
</script>

<style lang="less" scoped>
.header {
  display: flex;
  justify-content: space-between;
  height: 13.889vw;
  line-height: 13.889vw;
}
.iconjiantou2 {
  padding: 0 2.778vw;
}
.title {
  padding-right: 11.111vw;
  flex: 1;
  text-align: center;
  font-weight: bold;
}
</style>


